
.layout-wrap {
  width: 100%;
  padding-top: 3.5rem;
}
.layout-wrap .layout-head {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  background: #fff;
  z-index: 10;
}
.layout-wrap .layout-banner {
  background: #ffa500;
}
.layout-wrap .layout-banner .hero-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.layout-wrap .layout-banner .hero-content .left {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
  width: 60%;
  text-align: center;
}
.layout-wrap .layout-banner .hero-content .right {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 320px;
          flex: 0 0 320px;
  width: 320px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  height: 120px;
  padding: 10px;
  border: 1px solid #fff;
  border-radius: 10px;
}
.layout-wrap .layout-banner .hero-content .right img {
  display: block;
  height: 100%;
  margin-right: 10px;
}
.layout-wrap .layout-banner .hero-content .right .desc {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 auto;
          flex: 1 1 auto;
}
.layout-wrap .layout-submenu {
  width: 100%;
  background: #ffa500;
}
.layout-wrap .layout-submenu input[type="checkbox"] {
  display: none;
}
.layout-wrap .layout-submenu .container {
  position: relative;
  max-height: 3.5rem;
  overflow: hidden;
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.layout-wrap .layout-submenu .container .show-sub {
  margin: 0 auto;
  display: none;
}
.layout-wrap .layout-submenu .container .level {
  width: 100%;
  z-index: 5;
  line-height: 3rem;
}
.layout-wrap .layout-submenu input[type="checkbox"]:checked ~ .container {
  max-height: 100rem;
}
@media only screen and (max-width: 768px) {
.layout-banner .hero-body .hero-content .left {
    -webkit-box-flex: 0;
        -ms-flex: 0 0 100%;
            flex: 0 0 100%;
    width: 100%;
}
.layout-banner .hero-body .hero-content .right {
    display: none;
}
.layout-submenu .container .show-sub {
    display: block !important;
}
}
.slider-enter-active,
.slider-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.slider-enter-active .slider-wrap,
.slider-leave-active .slider-wrap {
  opacity: 1;
}
.slider-enter,
.slider-leave-to {
  opacity: 0;
}
.slider-enter .slider-wrap,
.slider-leave-to .slider-wrap {
  opacity: 1;
}
.slider {
  position: fixed;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  z-index: 10;
  background: rgba(0,0,0,0.4);
}
.slider .slider-wrap {
  position: absolute;
  width: 80%;
  height: 100%;
  padding: 10px;
  background: #fff;
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;
}
.slider .slider-wrap .top {
  margin-bottom: 1rem;
}
.slider .slider-wrap .top .user {
  text-align: center;
  padding: 1rem 0;
}
.slider .slider-wrap .top .user h3 {
  font-size: 1.8rem;
  font-weight: 700;
}
.slider .slider-wrap .top .user p {
  line-height: 1.6rem;
  font-size: 1.2rem;
}
.slider .slider-wrap .top .access {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 1rem;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}